</template>

<script>
import gotoPage from '@/helpers/gotoPage';
import numberFormat from '@/helpers/numberFormat';
import ColorBlock from '@/components/ColorBlock.vue';
import ItemCounter from '@/components/ItemCounter.vue';
import axios from 'axios';
import { API_BASE_URL } from '@/config';
import PreUploader from '@/components/PreUploader.vue';
import { mapActions } from 'vuex';

export default {
  data() {
    return {
      productAmount: 1,
      color: 0,
      productData: null,
      productLoading: false,
      productLoadingFailed: false,
      have404: false,
      productAdded: false,
      productAddSending: false,
    };
  },
  name: 'ProductPage',
  components: { ColorBlock, ItemCounter, PreUploader },
  filters: {
    numberFormat,
  },
  computed: {
    productColors() {
      return this.productData.colors;
    },
    product() {
      return this.productData;
    },
    category() {
      return this.productData.category;
    },
  },
  created() {
    this.loadProduct();
  },
  watch: {
    color(value) {
      // eslint-disable-next-line prefer-destructuring
      this.color = value;
    },
    have404() {
      this.$router.replace({ name: 'notFound' });
    },
    // eslint-disable-next-line func-names
    '$route.params.id': function () {
      this.loadProduct();
    },
  },
  methods: {
    ...mapActions(['addProductToCart']),
    gotoPage,
    addToCard() {
      this.productAdded = false;
      this.productAddSending = true;
      this.addProductToCart({ productId: this.product.id, amount: this.productAmount })
        .then(() => {
          this.productAdded = true;
          this.productAddSending = false;
        });
    },
    loadProduct() {
      this.productLoading = true;
      this.productLoadingFailed = false;

      this.have404 = false;
      clearTimeout(this.loadProductTimer);
      this.loadProductTimer = setTimeout(() => {
        axios.get(`${API_BASE_URL}/api/products/${this.$route.params.id}`)
          // eslint-disable-next-line no-return-assign,arrow-parens
          .then(response => this.productData = response.data)
          // eslint-disable-next-line no-return-assign
          .then(() => this.color = this.productData.colors[0].id)
          // eslint-disable-next-line no-return-assign
          .catch((err) => {
            this.productLoadingFailed = true;
            if (err.response.data.error.code === 404) {
              this.have404 = true;
            }
          })
          // eslint-disable-next-line no-return-assign
          .then(() => { this.productLoading = false; this.have404 = false; });
      }, 0);
    },
  },
};
</script>

